.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(49, 49, 49, 0.8);
}

.modal {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: black;
  border: white 2px solid;
  padding: 14px 28px;
  border-radius: 3px;
  max-width: 600px;
  min-width: 300px;
  z-index: 4;

}

.modal-content p {
  line-height: 1.4;

}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 0px 5px 3px 5px;

  border: white 1px solid;
  background: none;
  color: white;
  cursor: pointer;
}

.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 350px;
  height: 250px;
  border: 2px white solid;
  color: white;
  opacity: 1;
  z-index: 2;
  cursor: pointer;

}

.card-container h2 {
    z-index: 3;
    position: absolute;
}

.link-button {
    border: white 2px solid;
    background: none;
    color: white;
    cursor: pointer;
    padding: 5px;
    

}


.link-button a {
    color: white;
}

.head {
  z-index: 2;
  cursor: default;
}