.not-found h2 {
    font-size: 4rem;
}

.not-found p {
    font-size: 2rem;
}

.not-found {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.go-home-btn {
    background: none;
    color: white;
    padding: 5px;
    font-size: 1.5rem;
    cursor: pointer;
    margin-left: 20px;
    border: none;
    text-decoration: underline;

}

.go-home-btn:hover {
    font-size: 1.5rem;
    border: 2px white solid;
}

.not-found-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}